import type { IAnyError, ICryptoError } from '../types';

/**
 *       ,  .
 *  Firefox              .
 * @param {Error} error - ,     .
 * @returns {Error | undefined}  ,  .
 */
export function getLastError(
  error: ICryptoError | Error | IAnyError,
): Error | undefined {
  return window.cadesplugin?.getLastError(error);
}
